@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto-bold.woff2") format("woff2"), url("../fonts/roboto-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  font-family: "Roboto", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  overflow-x: hidden;
}

a {
  display: inline-flex;
  color: #000000;
  text-decoration: none;
  padding: 10px;
  justify-content: center;
  align-items: center;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  min-width: 400px;
  margin: 0 auto;
  padding: 0 405px;
}
@media (max-width: 1920px) {
  .container {
    padding: 0 21%;
  }
}
@media (max-width: 1200px) {
  .container {
    padding: 0 80px;
  }
}
@media (max-width: 800px) {
  .container {
    padding: 0 30px;
  }
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  color: #000000;
  background-color: #ffffff;
  cursor: pointer;
  border: 2px solid #66c05d;
  transition: opacity 0.3s ease;
  border-radius: 40px;
  min-width: 170px;
  min-height: 40px;
}
@media (max-width: 800px) {
  .btn {
    min-width: 0;
    max-width: 150px;
  }
}
.btn:hover {
  color: #66c05d;
}
.btn--green {
  min-width: 130px;
  min-height: 35px;
  background-color: #66c05d;
  color: #ffffff;
  padding: 5px;
}
@media (max-width: 800px) {
  .btn--green {
    min-width: 150px;
  }
}
.btn--green:hover {
  border: 2px solid #000000;
  color: #000000;
}
.btn--white {
  display: inline-flex;
  background-color: #66c05d;
  color: #ffffff;
  border: 2px solid #ffffff;
}
.btn--white:hover {
  border-color: #000000;
  color: #000000;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.header {
  border-bottom: 1px solid #efefef;
}

.header__nav {
  background-color: #e5e5e5;
}

.header__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 50px;
  gap: 10px;
  flex-wrap: wrap;
  max-width: 600px;
  margin: 0 auto;
}

.header__link {
  line-height: 16px;
  color: #333333;
}
.header__link:hover {
  color: #66c05d;
}

.logo-link {
  margin: 7px 0;
}
@media (max-width: 800px) {
  .logo-link {
    width: 140px;
    height: 40px;
  }
}

.page-header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.back-cool {
  display: flex;
  gap: 20px;
}
@media (max-width: 1200px) {
  .back-cool {
    flex-direction: column;
    margin-bottom: 20px;
    gap: 10px;
  }
}

.back-cool__link:hover {
  color: #66c05d;
}

.main-nav {
  position: relative;
  z-index: 1;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 80px;
  gap: 10px;
  flex-wrap: wrap;
}

.main-nav__link:hover {
  color: #66c05d;
}

.super-offer {
  background-color: #66c05d;
  color: #ffffff;
}
@media (max-width: 1920px) {
  .super-offer {
    min-height: 33vw;
    padding-bottom: 20px;
  }
}

.super-offer__title {
  font-weight: 700;
  font-size: 48px;
  line-height: 56px;
}
@media (max-width: 800px) {
  .super-offer__title {
    font-size: 26px;
    line-height: 26px;
  }
}

.super-offer__wrapper-description {
  max-width: 310px;
}

.super-offer__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.super-offer__wrapper-img {
  position: relative;
  margin-right: 68px;
}
@media (max-width: 1700px) {
  .super-offer__wrapper-img {
    margin: 0;
  }
}
.super-offer__wrapper-img::before, .super-offer__wrapper-img::after {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  border-radius: 50%;
}
.super-offer__wrapper-img::before {
  width: 147%;
  height: 147%;
  left: -17%;
  background: rgba(255, 255, 255, 0.3);
}
.super-offer__wrapper-img::after {
  width: 116%;
  height: 116%;
  left: -2%;
  background: rgba(255, 255, 255, 0.4);
}

.super-offer__img {
  position: relative;
  z-index: 1;
}

.special-offers {
  position: relative;
  z-index: 2;
  background-color: #efefef;
  padding: 50px 20px;
  padding-bottom: 130px;
}

.special-offers__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: relative;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 30px;
  margin-bottom: 100px;
}
@media (max-width: 1550px) {
  .special-offers__list {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
  }
}
@media (max-width: 800px) {
  .special-offers__list {
    grid-template-columns: 1fr;
  }
}
.special-offers__list::after {
  position: absolute;
  content: "";
  width: 190px;
  height: 62px;
  right: 0;
  top: -77px;
  background: url("../img/svg/icon-offers.svg") center no-repeat;
  background-size: 100% 100%;
}
@media (max-width: 800px) {
  .special-offers__list::after {
    width: 95px;
    height: 32px;
    top: -45px;
  }
}
@media (max-width: 500px) {
  .special-offers__list::after {
    display: none;
  }
}

@media (max-width: 800px) {
  .special-offers__title {
    text-align: center;
  }
}

.offer-card {
  background-color: #ffffff;
  padding: 25px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.offer-card:hover {
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
}

.offer-card__img-wrapper {
  width: 125px;
  height: 166px;
}

.offer-card__description {
  text-align: center;
}

.gifts-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 30px;
  color: #ffffff;
}
@media (max-width: 800px) {
  .gifts-list {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }
}

.gifts-list__item {
  padding: 35px 38% 35px 40px;
  background-color: #66c05d;
}
.gifts-list__item--email {
  position: relative;
}
.gifts-list__item--email::after {
  position: absolute;
  top: 25%;
  right: 8%;
  content: "";
  width: 27%;
  height: 50%;
  background: url("../img/svg/e-mail.svg") center no-repeat;
  background-size: 100% 100%;
}
@media (max-width: 600px) {
  .gifts-list__item--email::after {
    width: 17%;
    height: 25%;
  }
}

.gifts-list__input {
  border: none;
  border-radius: 30px;
  width: 225px;
  min-height: 35px;
  padding: 0 25px;
}
.gifts-list__input::placeholder {
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: #c4c4c4;
}

.gifts-list__item-wrapper {
  max-width: 253px;
}

.gifts-list__title {
  font-size: 18px;
}

@media (max-width: 1100px) {
  .gifts-list__description br {
    display: none;
  }
}

.futer__container {
  min-height: 120px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 900px) {
  .futer__container {
    flex-direction: column;
  }
}

@media (max-width: 1500px) {
  .futer__back-cool {
    flex-direction: column;
  }
}

.social {
  display: flex;
  max-width: 265px;
  flex-wrap: wrap;
}

.social__icon {
  stroke: #66c05d;
  fill: #66c05d;
}

.social__link:hover .social__icon {
  stroke: #333333;
  fill: #333333;
}

.social + address {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
}
@media (max-width: 900px) {
  .social + address {
    margin-top: 20px;
  }
}

.copyright {
  background-color: #efefef;
  padding: 30px 0;
}

.copyright__container {
  min-height: 62px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.copyright__container span,
.copyright__container a {
  text-align: center;
}
.copyright__container a:hover {
  color: #66c05d;
}
@media (max-width: 900px) {
  .copyright__container {
    flex-direction: column;
  }
}

